/*@import "node_modules/react-image-gallery/styles/css/image-gallery.css";*/

.App {
  text-align: center;
  color: white;
}

header {
  position: relative;
  height: 300px;
  padding-top: 50px;
  background-image: url("./back.jpg");
}

header img {
  height: 100px;
  width: 100px;
  border-radius: 50px;
  border: 3px solid white;
  object-fit: cover;
}

.purple-section {
  background-color: rgb(118, 79, 220);
}

h1 {
  font-size: 2.3rem;
  margin-bottom: 0;
}

h3 {
  margin-top: 0;
  font-weight: normal;
  font-size: 2rem;
}

h2 {
  color: rgb(118, 79, 220);
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 50px;
  margin-top: 30px;
}


.share-button{
  width: 100px;
  position: absolute;
  top: -22px;
  right: 68px;
}


section.gallery {
  margin-left: 15%;
  margin-right: 15%;
  margin-bottom: 30px;
  position: relative;
}

.attendee-image {
  height: 250px;
  margin: 3px;
  object-fit: cover;
  cursor: pointer;
}

section {
  text-align: center;
}

section.sponsors {
  padding-top: 20px;
  background-color: rgb(201, 191, 240);
  text-align: center;
  width: 100%;
}

section.next-event {
  margin-top: 2px;
  padding-top: 20px;
  height: 200px;
  background-color: rgb(118, 79, 220);
  margin-bottom: 200px;
}

section.sponsors h2, section.next-event h2 {
  color: white;
  width: 25%;
  margin-left: 37%;
}

.sponsors-container {
  width: 100%;
}

.sponsor-image {
  width: 180px;
  height: 80px;
  object-fit: cover;
  margin: 10px 20px;
}

button {
  color: white;
  background-color: rgb(227, 96, 104);
  border: none;
  width: 10%;
  height: 30px;
  border-radius: 20px;
  outline: none;
}

@media (max-width: 768px) {
  .share-button {
    top: 92px;
    right: 34%;
  }

  h2 {
    margin-bottom: 70px;
  }

  section.sponsors h2, section.next-event h2 {
    width: 90%;
    margin-left: 5%;
    margin-bottom: 20px;
  }

  button {
    width: 75%;
    height: 50px;
  }
}

.dudu{
  position: fixed;
  bottom: 0;
  left: 20%;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("https://s3.amazonaws.com/bizzathon-2018-audio/dudu.jpg");
  object-fit: cover;
  width: 100%;
  height: 80vh;
}